const cardVariants = (
    x0 = 0.0,
    y0 = 0.0,
    x1 = 0.0,
    y1 = 0.0,
    duration = 1.0,
    bounce = 0.4,
    initialRotation = "0deg",
    finalRotation = "0deg",
    delay = 0.0
  ) => {
    return {
      offscreen: {
        x: x0,
        y: y0,
        opacity: 0,
        rotate: initialRotation,
        transition: {
          rotate: initialRotation,
        },
      },
      onscreen: {
        x: x1,
        y: y1,
        opacity: 1,
        rotate: finalRotation,
        transition: {
          type: "spring",
          bounce: bounce,
          duration: duration,
          rotate: finalRotation,
          delay: delay
        },
      },
    };
  };

export {
    cardVariants,
}