import React from "react";
import "./DesktopAppShowCase.scss";
import { Typography } from "@mui/material";
import { motion } from "framer-motion";
import SkillCard from "../SkillCard/SkillCard";
import { cardVariants } from "../../../utils/motionUtils";
import MotionPath from "../MotionPath/MotionPath";
const DesktopAppShowCase = ({ projectInfo, isMobile }) => {
  return (
    <div className="desktop-app-container">
      {/* <MotionPath variants={cardVariants()} x1={"50%"} y1={"10%"} x2={"50%"} y2={"100%"} /> */}
      <motion.div
        className="motion-card-parent motion-card-left"
        initial="offscreen"
        whileInView="onscreen"
        viewport={{ once: true, amount: 0.3 }}
      >
        <motion.div variants={cardVariants()} className="motion-card-child">
          <Typography fontFamily={'Sora'} variant={"h4"}>{projectInfo?.title}</Typography>
          <Typography className='body-text' >{projectInfo?.description}</Typography>
        </motion.div>
        <div className="zStack">
          {projectInfo?.assets.map((asset, i) => {
            return <motion.img
            key={i}
            className={i % 2 === 0 ? "motion-pic zStack-e left-pic-e" : "motion-pic zStack-e right-pic-e"}
            src={asset.src}
            variants={cardVariants(-20, `calc(${i} * ${80}%)`, `calc(${i} * ${5}%)`, `calc(${i} * ${80}%)`, 0.8, 0.4, "0deg", "0deg", i * 0.8)}
          />
          })}
        </div>
      </motion.div>

      <motion.div
        className="tech-stack-div"
        initial="offscreen"
        whileInView="onscreen"
        viewport={{ once: true, amount: 0.4 }}
        variants={cardVariants()}
      >
        <div className="hStack-tech-stack">
          {projectInfo?.skills.map((skill, i) => {
            return <SkillCard key={i} svg={skill?.svg} text={skill?.name} cardVariants={cardVariants(0, 10, 0, 0, 1.0, 0.4, "0deg", "0deg", i * 0.2)} />;
          })}
        </div>
      </motion.div>
    </div>
  );
};

export default DesktopAppShowCase;
