import React from "react";
import "./MobileAppShowCase.scss";
import { Typography } from "@mui/material";
import { motion } from "framer-motion";
import SkillCard from "../SkillCard/SkillCard";
import { cardVariants } from "../../../utils/motionUtils";
import MotionPath from "../MotionPath/MotionPath";

const MobileAppShowCase = ({ projectInfo, isMobile }) => {
  return (
    <div className="mobile-app-container">
      {/* <MotionPath variants={cardVariants()} x1={"50%"} y1={"10%"} x2={"50%"} y2={"100%"} /> */}
      <motion.div
        className="motion-card-parent motion-card-center"
        initial="offscreen"
        whileInView="onscreen"
        viewport={{ once: true, amount: 0.3 }}
      >
        <motion.div variants={cardVariants()} className="motion-card-child">
          <Typography fontFamily={'Sora'} variant={"h4"}>{projectInfo?.title}</Typography>
          <Typography className="body-text">{projectInfo?.description}</Typography>
        </motion.div>
        <div className={"hStack-pic"}>
          {projectInfo?.assets.map((asset, i) => {
              if (asset.type === "image") {
                return (
                  <motion.img
                    key={i}
                    src={asset.src}
                    className="motion-pic zStack-pic-e"
                    variants={cardVariants(0, 0, 0, 0, 1.0, 0.4, "0deg", `${(i - 1) * 10}deg`)}
                    width={"20%"}
                  />
                );
              } else if (asset.type === "video") {
                return (
                  <motion.video
                    key={i}
                    className="motion-video zStack-pic-e"
                    variants={cardVariants(0, 0, 0, 0, 1.0, 0.4, "0deg", `${(i - 1) * 10}deg`)}
                    controls={false}
                    autoPlay
                    muted
                  >
                    <source src={asset.src} type="video/mp4" />
                    Your browser does not support the video tag.
                  </motion.video>
                );
              }
            })}
        </div>
      </motion.div>

      <motion.div
        className="tech-stack-div"
        initial="offscreen"
        whileInView="onscreen"
        viewport={{ once: true, amount: 0.4 }}
        variants={cardVariants()}
      >
        <div className="hStack-tech-stack">
          {projectInfo?.skills.map((skill, i) => {
            return <SkillCard key={i} svg={skill?.svg} text={skill?.name} cardVariants={cardVariants(0, 10, 0, 0, 1.0, 0.4, "0deg", "0deg", i * 0.2)} />;
          })}
        </div>
      </motion.div>
    </div>
  );
};

export default MobileAppShowCase;
